import React from "react";
import { NavLink } from "react-router-dom";

const Header = () => (
  <header>
    <h1>Expensify</h1>
    <NavLink to="/" exact={true} activeClassName="is-active">
      Home
    </NavLink>
    <NavLink to="/create" exact={true} activeClassName="is-active">
      Create
    </NavLink>
    <NavLink to="/help" exact={true} activeClassName="is-active">
      Help
    </NavLink>
  </header>
);

export default Header;
